.banner {


    .swiper-slide {
        background-position: center;
    }

    .container {
        min-height: 700px;
        padding: 0 12px 63px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }

    &-title {
        font-size: 56px;
        font-weight: 700;
        max-width: 463px;
        margin-bottom: 8px;
        line-height: 126%;
        text-transform: uppercase;
    }

    &-description {
        font-weight: 300;
        font-size: 20px;
        line-height: 135%;
        max-width: 340px;
        margin-bottom: 37px;
    }

    .swiper-button-prev,
    .swiper-button-next {
        height: 0;
        max-width: 1598px;
        padding: 0 50px;
        width: 100%;
        left: 50%;
        transform: translate(-50%, 23px);
        justify-content: flex-start;
        color: #fff;

        &:hover {
            color: $secondary-color
        }
    }

    .swiper-button-next {
        justify-content: flex-end;
    }

    .swiper-horizontal>.swiper-pagination-bullets,
    .swiper-pagination-bullets.swiper-pagination-horizontal,
    .swiper-pagination-custom,
    .swiper-pagination-fraction {
        bottom: 43px;
    }

}
