.custom-navbar {
    padding: 21px 0 33px;

    .navbar-brand {
        margin: 0;
    }

    .navbar-nav {
        margin-top: 3px;
    }

    .nav-item {
        .nav-link {
            padding: 12px 23px;

            color: #fff;

            &:hover {
                text-decoration: none;
                color: $primary-color;
            }
        }
    }

    .navbar-toggler {
        border: none;
        border-radius: 0;
        padding: 0;
        box-shadow: none;
    }

    .navbar-toggler-icon {
        position: relative;
        background: #fff;
        height: 2px;
        width: 28px;
        margin: -23px 0 0 0;

        &:before,
        &:after {
            content: "";
            position: absolute;
            left: 0;
            top: 11px;
            width: 100%;
            height: 2px;
            background: #fff;
            transition: .3s;
        }

        &:after {
            top: 22px;
        }
    }

    [aria-expanded="true"] {
        .navbar-toggler-icon {
            background-color: transparent;



            &:before {
                transform: rotate(45deg);
            }

            &:after {
                transform: rotate(-45deg);
                top: 11px;
            }
        }
    }
}
